// src/components/services/ServiceEcosystem.tsx
'use client';

import { useState } from 'react';
import { Network, Cpu, Database, Cloud, Smartphone, Users } from 'lucide-react';

const ServiceEcosystem = () => {
  const [activeNode, setActiveNode] = useState<string | null>(null);
  
  const nodes = [
    {
      id: 'iot',
      title: 'IoT & Capteurs',
      description: 'Collecte de donn√©es en temps r√©el',
      icon: <Cpu className="w-8 h-8" />,
      position: 'top-1/2 left-1/4',
      color: 'blue'
    },
    {
      id: 'data',
      title: 'Traitement Data',
      description: 'Analyse et transformation des donn√©es',
      icon: <Database className="w-8 h-8" />,
      position: 'top-1/3 left-1/2',
      color: 'purple'
    },
    {
      id: 'ia',
      title: 'Intelligence Artificielle',
      description: 'Algorithmes pr√©dictifs et automatisation',
      icon: <Network className="w-8 h-8" />,
      position: 'top-2/3 left-1/2',
      color: 'green'
    },
    {
      id: 'cloud',
      title: 'Cloud & Infrastructure',
      description: 'H√©bergement scalable et s√©curis√©',
      icon: <Cloud className="w-8 h-8" />,
      position: 'top-1/2 right-1/4',
      color: 'orange'
    },
    {
      id: 'apps',
      title: 'Applications',
      description: 'Web, mobile et interfaces utilisateur',
      icon: <Smartphone className="w-8 h-8" />,
      position: 'bottom-10 left-1/3',
      color: 'indigo'
    },
    {
      id: 'users',
      title: 'Utilisateurs',
      description: 'Exp√©rience et valeur business',
      icon: <Users className="w-8 h-8" />,
      position: 'bottom-10 right-1/3',
      color: 'red'
    }
  ];
  
  const connections = [
    { from: 'iot', to: 'data' },
    { from: 'data', to: 'ia' },
    { from: 'ia', to: 'cloud' },
    { from: 'cloud', to: 'apps' },
    { from: 'apps', to: 'users' },
    { from: 'data', to: 'apps' },
    { from: 'ia', to: 'users' }
  ];
  
  const getColorClass = (color: string) => {
    const colors: Record<string, string> = {
      blue: 'bg-blue-500 border-blue-500',
      purple: 'bg-purple-500 border-purple-500',
      green: 'bg-green-500 border-green-500',
      orange: 'bg-orange-500 border-orange-500',
      indigo: 'bg-indigo-500 border-indigo-500',
      red: 'bg-red-500 border-red-500'
    };
    return colors[color] || colors.blue;
  };

  return (
    <section className="py-20">
      <div className="container px-4 mx-auto">
        <div className="text-center mb-12">
          <h2 className="text-3xl font-bold text-gray-900 mb-4">
            Notre √©cosyst√®me technologique int√©gr√©
          </h2>
          <p className="text-gray-600 max-w-2xl mx-auto">
            Toutes nos solutions sont interconnect√©es pour offrir une exp√©rience compl√®te et coh√©rente
          </p>
        </div>
        
        {/* Interactive Ecosystem Visualization */}
        <div className="relative h-[600px] bg-gradient-to-b from-gray-50 to-white rounded-3xl border border-gray-200 overflow-hidden">
          {/* Connections */}
          <svg className="absolute inset-0 w-full h-full">
            {connections.map((conn, index) => {
              const fromNode = nodes.find(n => n.id === conn.from);
              const toNode = nodes.find(n => n.id === conn.to);
              
              if (!fromNode || !toNode) return null;
              
              return (
                <line
                  key={index}
                  x1="25%"
                  y1="50%"
                  x2="50%"
                  y2="33%"
                  stroke={activeNode === conn.from || activeNode === conn.to ? '#3b82f6' : '#d1d5db'}
                  strokeWidth="2"
                  className="transition-colors duration-300"
                />
              );
            })}
          </svg>
          
          {/* Nodes */}
          {nodes.map((node) => (
            <div
              key={node.id}
              className={`absolute ${node.position} transform -translate-x-1/2 -translate-y-1/2`}
              onMouseEnter={() => setActiveNode(node.id)}
              onMouseLeave={() => setActiveNode(null)}
            >
              <div className={`
                relative w-24 h-24 rounded-full border-4 
                ${getColorClass(node.color).split(' ')[1]}
                ${activeNode === node.id ? 'scale-110' : ''}
                transition-all duration-300
                flex items-center justify-center
                ${getColorClass(node.color).split(' ')[0]}
                cursor-pointer group
              `}>
                <div className="text-white">
                  {node.icon}
                </div>
                
                {/* Tooltip */}
                <div className="absolute -bottom-24 left-1/2 transform -translate-x-1/2 w-64 p-4 bg-white rounded-lg shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-300 z-10">
                  <h4 className="font-bold text-gray-900 mb-1">{node.title}</h4>
                  <p className="text-sm text-gray-600">{node.description}</p>
                  <div className="absolute -top-2 left-1/2 transform -translate-x-1/2 w-4 h-4 bg-white rotate-45" />
                </div>
              </div>
            </div>
          ))}
          
          {/* Central Hub */}
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2">
            <div className="relative">
              <div className="w-32 h-32 rounded-full bg-gradient-to-r from-primary-600 to-primary-800 border-8 border-white shadow-2xl flex items-center justify-center">
                <span className="text-white text-2xl font-bold">VIBECRO</span>
              </div>
              
              {/* Pulsing animation */}
              <div className="absolute inset-0 rounded-full bg-primary-400 animate-ping opacity-20" />
            </div>
          </div>
        </div>
        
        {/* Legend */}
        <div className="mt-12">
          <h4 className="text-center font-semibold text-gray-700 mb-6">Flux de donn√©es</h4>
          <div className="flex flex-wrap justify-center gap-6">
            {connections.map((conn, index) => (
              <div key={index} className="flex items-center">
                <div className="w-4 h-0.5 bg-gray-300 mr-2" />
                <span className="text-sm text-gray-600">
                  {conn.from} ‚Üí {conn.to}
                </span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default ServiceEcosystem;